      <HomeTab v-if="currentTab === 0" />
      <DocumentsTab v-else-if="currentTab === 1" />
      <TeamTab v-else-if="currentTab === 3" />
      <ProfileTab v-else-if="currentTab === 4" />
    </keep-alive>

    <!-- Ëá™ÂÆö‰πâÂ∫ïÈÉ®ÂØºËà™Ê†è -->
    <view class="custom-tabbar" :style="{ paddingBottom: safeBottom }">
      <!-- È¶ñÈ°µ -->
      <view class="tabbar-item" @click="switchTab(0)">
        <view class="tabbar-icon">üè†</view>
        <text class="tabbar-text" :class="{ active: currentTab === 0 }">È¶ñÈ°µ</text>
      </view>
      <!-- Â™í‰Ωì -->
      <view class="tabbar-item" @click="switchTab(1)">
        <view class="tabbar-icon">üìÑ</view>
        <text class="tabbar-text" :class="{ active: currentTab === 1 }">ÂÆûË∑µ</text>
      </view>
      <!-- Âç†‰ΩçÁ¨¶ÔºàÂπ≥Ë°°AIÊåâÈíÆÂ±Ö‰∏≠Ôºâ -->
      <view class="tabbar-spacer"></view>

      <!-- Â∑•ÂÖ∑ÁÆ±ÊåâÈíÆÔºàÊöÇÊó∂Á¶ÅÁî®AIÂäüËÉΩÔºâ -->
      <view class="tabbar-item ai-button" @click="showComingSoon">
        <view class="toolbox-circle">
          <!-- ÂúÜÂΩ¢Ë£ÅÂâ™LogoÔºå‰ºòÂÖàÁî®È¢ÑÂ§ÑÁêÜÂ•ΩÁöÑÂúÜÂΩ¢Âõæ -->
          <image 
            class="toolbox-img" 
            src="https://llzm-lixiaobao.oss-cn-beijing.aliyuncs.com/logo.jpg" 
            mode="aspectFill" 
            @error="handleImgError"
          />
        </view>
      </view>

      <!-- Âç†‰ΩçÁ¨¶ÔºàÂπ≥Ë°°AIÊåâÈíÆÂ±Ö‰∏≠Ôºâ -->
      <view class="tabbar-spacer"></view>
      <!-- Âõ¢Èòü -->
      <view class="tabbar-item" @click="switchTab(3)">
        <view class="tabbar-icon">üë•</view>
        <text class="tabbar-text" :class="{ active: currentTab === 3 }">Âõ¢Èòü</text>
      </view>
      <!-- ÊàëÁöÑ -->
      <view class="tabbar-item" @click="switchTab(4)">
        <view class="tabbar-icon profile-icon">üë§</view>
        <text class="tabbar-text" :class="{ active: currentTab === 4 }">Êàë</text>
      </view>
    </view>
