<script>
// ÂºïÂÖ•ÂêÑÈ°µÈù¢ÁªÑ‰ª∂ÔºàÁßªÈô§AiTabÁªÑ‰ª∂ÂºïÁî®ÔºåÂõ†‰∏∫ÂÆÉÊòØÁã¨Á´ãÈ°µÈù¢Ôºâ
import HomeTab from './HomeTab.vue'
import DocumentsTab from './DocumentsTab.vue'
import TeamTab from './TeamTab.vue'
import ProfileTab from './ProfileTab.vue'
import careModeMixin from '@/mixins/careMode.js'

export default {
  mixins: [careModeMixin],
  name: 'TabBarContainer',
  components: { HomeTab, DocumentsTab, TeamTab, ProfileTab },
  data() {
    return {
      currentTab: 0, // ÂΩìÂâçÊøÄÊ¥ªÁöÑÊ†áÁ≠æ
      safeBottom: '0px', // ÂÆâÂÖ®Âå∫ÂüüÂ∫ïÈÉ®Èó¥Ë∑ù
      tabbarHeight: '120rpx' // ÂØºËà™Ê†èÈ´òÂ∫¶
    }
  },
  methods: {
    // ÂàáÊç¢Ê†áÁ≠æÈÄªËæë
    switchTab(idx) {
      this.currentTab = idx;
      
      // Â¶ÇÊûúÊòØÂàáÊç¢Âà∞‰∏™‰∫∫‰∏≠ÂøÉÔºåÊ£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
      if (idx === 4) {
        setTimeout(() => {
          this.checkLoginStatus();
        }, 100);
      }
    },
    // Ë∑≥ËΩ¨Âà∞È°πÁõÆ‰∏≠ÂøÉÈ°µÈù¢
    showComingSoon() {
      uni.navigateTo({
        url: '/pages/index/AiTab',
        fail: (err) => {
          console.error('Ë∑≥ËΩ¨È°πÁõÆ‰∏≠ÂøÉÂ§±Ë¥•:', err)
          uni.showToast({
            title: 'È°µÈù¢‰∏çÂ≠òÂú®',
            icon: 'none',
            duration: 2000
          })
        }
      })
    },
    // Â§ÑÁêÜÂõæÁâáÂä†ËΩΩÈîôËØØ
    handleImgError(e) {
      e.target.src = '/static/logo.jpg' // ÊõøÊç¢‰∏∫ÈªòËÆ§ÂõæÁâáË∑ØÂæÑ
    },
    
    // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
    checkLoginStatus() {
      const isLoggedIn = uni.getStorageSync('isLoggedIn')
      // ‰∏çÂº∫Âà∂Ë∑≥ËΩ¨ÔºåËÆ©Áî®Êà∑ÂÖàÁúãÂà∞È¶ñÈ°µÂÜÖÂÆπ
    }
  },
  onLoad(options) {
    // Â§ÑÁêÜtabÂèÇÊï∞
    if (options.tab) {
      const tabIndex = parseInt(options.tab);
      if (tabIndex >= 0 && tabIndex <= 4) {
        this.currentTab = tabIndex;
      }
    }
    
    // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
    this.checkLoginStatus()
    
    // Ëé∑ÂèñËÆæÂ§áÂÆâÂÖ®Âå∫Âüü‰ø°ÊÅØ
    try {
      const sysInfo = uni.getSystemInfoSync()
      this.safeBottom = sysInfo.safeAreaInsets 
        ? `${sysInfo.safeAreaInsets.bottom}px` 
        : '0px'
      // ËÆ°ÁÆóÂØºËà™Ê†èÊÄªÈ´òÂ∫¶ÔºàÂü∫Á°ÄÈ´òÂ∫¶ + ÂÆâÂÖ®Âå∫ÂüüÂ∫ïÈÉ®Ôºâ
      this.tabbarHeight = `${120 + (sysInfo.safeAreaInsets?.bottom || 0)}rpx`
    } catch (e) {
      console.error('Ëé∑ÂèñÁ≥ªÁªü‰ø°ÊÅØÂ§±Ë¥•:', e)
    }
  },
  
  onShow() {
    // ‰∏çÂº∫Âà∂Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅÔºåËÆ©Áî®Êà∑Ëá™Áî±ÊµèËßà
  }
}
</script>

